<template><section><h1>VcShineEllipse</h1>
<p>The <code v-pre>vc-shine-ellipse</code> component is used to load the flash circle effect. Refer to <a href="https://www.wellyyss.cn/ysCesium/main/app.html">ysCesium|跃焱邵隼</a>.</p>
<h2>Example</h2>
<h3>Load a VcShineEllipse</h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-shine-ellipse @ready="subReady" :position="position" color="red"></vc-shine-ellipse>
        <vc-layer-imagery>
          <vc-provider-imagery-tianditu mapStyle="img_c" token="436ce7e50d27eede2f2929307e6b33c0"></vc-provider-imagery-tianditu>
        </vc-layer-imagery>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
