    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">position</span>: { <span class="hljs-attr">lng</span>: <span class="hljs-number">117.217124</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">31.809777</span> }
      }
    },
    <span class="hljs-attr">methods</span>: {
      ready(cesiumInstance) {
        <span class="hljs-keyword">this</span>.cesiumInstance = cesiumInstance
      },
      subReady() {
        <span class="hljs-keyword">const</span> { Cesium, viewer } = <span class="hljs-keyword">this</span>.cesiumInstance
        viewer.scene.globe.depthTestAgainstTerrain = <span class="hljs-literal">true</span>
        viewer.zoomTo(viewer.entities)
      }
    }
  }
