<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>position</td>
<td>Object</td>
<td></td>
<td><code v-pre>required</code> Specify the location where the flash circle effect entity is added. structure:{ lng: number, lat: number, height: number } or Cesium.Cartesian3</td>
</tr>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> Specifies whether the ellipse circle ripple effect is visible.</td>
</tr>
<tr>
<td>height</td>
<td>Number</td>
<td><code v-pre>undefined</code></td>
<td><code v-pre>optional</code> Specify the height of the ellipse for the flash circle effect in meters.</td>
</tr>
<tr>
<td>radius</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> Specify the flash circle effect radius in meters.</td>
</tr>
<tr>
<td>color</td>
<td>Object|String|Array</td>
<td><code v-pre>'white'</code></td>
<td><code v-pre>optional</code> Specify the color of the flash circle effect.</td>
</tr>
<tr>
<td>imageUrl</td>
<td>String</td>
<td><code v-pre>''</code></td>
<td><code v-pre>optional</code> Specify the material image used to express the flash circle effect.</td>
</tr>
<tr>
<td>deviationAlpha</td>
<td>Number</td>
<td><code v-pre>0.05</code></td>
<td><code v-pre>optional</code> Specify the flash circle effect color Alpha value to change the scale, the larger the value, the faster the flash. Ranges: (0, 1)</td>
</tr>
</tbody>
</table>
<hr>
<h2>Events</h2>
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject }</td>
<td>Triggers when the component is ready. It returns a core class of Cesium, a viewer instance, and the cesiumObject.</td>
</tr>
</tbody>
</table>
</section></template>

<script>
    export default {
      data() {
        return {
          position: { lng: 117.217124, lat: 31.809777 }
        }
      },
      methods: {
        ready(cesiumInstance) {
          this.cesiumInstance = cesiumInstance
        },
        subReady() {
          const { Cesium, viewer } = this.cesiumInstance
          viewer.scene.globe.depthTestAgainstTerrain = true
          viewer.zoomTo(viewer.entities)
        }
      }
    }
  </script>